$yellow: #f8fc74;
$red: #fd625c;
$light_red: #ff736d;
$pink_light: #fcf7f7;
$pink_dark: #ffd4d3;

$sansSerifFont: "Roboto", "Segoe UI", "Oxygen",
"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
sans-serif;
$serifFont: "Roboto Slab", "Times", "Times New Roman", "Palatino", "Georgia", serif;

$squareBoxShadow: 3px 3px 0 0 #000;
$transition200ms: ease-in-out 200ms;

// The width of the screen when a burger menu shall be shown instead of navigation buttons
$burgerMenuWidth: 950px;
$stopBurgerScrollWidth: 440px;

$websitePaddingTop40: 40px;
$websitePaddingLeft40: 40px;

$websitePaddingTop34: 34px;
$websitePaddingLeft20: 20px;

$header2_5: 2.5rem;
$header2: 2rem;
$header1_8: 1.8rem;
$header1_5: 1.5rem;

$header_margin_bottom80: 80px;
$header_margin_bottom50: 50px;
$header_margin_bottom30: 30px;

$header_padding_top30: 30px;

$headerMinHeight120: 120px;

$headerMaxWidth90: 90%;
$headerMaxWidth80: 80%;
$headerMaxWidth60: 60%;

$footerBottomGap140: 140px;
$footerBottomGap100: 100px;

$pageNavDistance40: 40px;
$pageNavDistance20: 20px;
$pageNavDistance15: 15px;
$pageNavDistance10: 10px;
$pageNavDistance5: 5px;

$donateLightGrey: rgb(212, 212, 212);
$donateDarkGrey: rgb(133, 133, 133);

// The way buttons react to being hovered and pressed
@mixin scalableButtons0_1 {
  &:hover {
    transform: scale(1.1);
  }

  &:active {
    transform: scale(0.9);
  }
}

@mixin scalableButtons0_05 {
  &:hover {
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }
}

@mixin scalableButtons0_03 {
  &:hover {
    transform: scale(1.03);
  }

  &:active {
    transform: scale(0.97);
  }
}

@mixin object_fit {
  object-fit: contain;
  object-position: center;
}

@mixin list_reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin list_reset_number {
  margin: 0;
  padding: 0;
}

@mixin flex_column_top_center {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

@mixin flex_column_top_left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

@mixin flex_row_left_center {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

@mixin grey_bottom_line {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

// Standard link: black when not active and yellow when hovered
.standardLink:link,
.standardLink:visited {
  color: #000000;
  font-weight: bold;
}

.standardLink:hover {
  background-color: $yellow;
}

// Standard header, main header on each webpage except for donate webpage
.standardHeader {
  max-width: 575px;
  padding: 23px 0 0 0;
  font-size: $header2_5;
  font-weight: 700;
  text-align: center;
  font-family: "Roboto Slab";
  font-weight: 700;
}

@media(max-width: 950px) {
  .standardHeader {
    padding: 100px 0 0 0;
  }
}

@media (max-width: 545px) {
  .standardHeader {
    font-size: $header2;
  }
}

@media(max-width: 480px) {
  .standardHeader {
    padding-top: 80px;
  }
}

@media (max-width: 360px) {
  .standardHeader {
    font-size: $header1_8;
  }
}

// Provides a page container for all website pages. It has a padding allowing
// the page navigation to look good and not to be overlapped by other site components
.page-container {
  margin: 0 0 $footerBottomGap140 0;
}

// @media(max-width: 1170px) {
//   .page-container {
//     margin: 0 150px $footerBottomGap160 150px;
//   }
// }

@media(max-width: $burgerMenuWidth) {
  .page-container {
    margin: 0 0 $footerBottomGap100 0;
  }
}

@media(max-width: 585px) {
  .page-container {
    margin: 0 30px $footerBottomGap100 30px;
  }
}

@media(max-width: 440px) {
  .page-container {
    margin: 0 0 $footerBottomGap100 0;
  }
}
