.screenshot__picture-container_1024_512 {
  .dateList {
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    margin: 0 90px 20px;
  }

  .dateList__item {
    width: 220px;
    // height: 36px;
    margin-bottom: 0;
    padding: 10px;
  }

  .dateList__item:not(:last-child) {
    margin-right: 10px;
  }

  .dateList__item--start {
    order: 1;
  }

  .dateList__item--days {
    order: 2;
  }

  .dateList__item--end {
    order: 3;
  }

  .dateList__important-text--bigger,
  .dateList__important-text {
    margin: 0 0 7px 0;
    font-size: 17px;
  }

  .dateList__caption {
    font-size: 13px;
  }
}

